* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}


body {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    background-color: #f8f8f8;
    font-size: 12px;
    font-family: Arial, Helvetica, sans-serif;
}

h1 {
    display: flex;
    justify-content: center;
    font-size: 2.4rem;
    color: rgb(92, 92, 92);
    margin: 20px 0 0 0;
}

h4 {
    font-size: 1.4rem;
    font-weight: bold;
    color: rgb(92, 92, 92);
    margin: 30px 0 10px 0;
}


p {
    font-size: 1.1rem;
    color:rgb(120, 120, 120);
    line-height: 1.8rem;
    text-align: justify;
}

input {
    height: 40px;
    width: 100%;
    border-radius: 6px;
    border: 1px solid #333;
    padding: 6px 8px;
    font-size: 1.1rem;
    margin-bottom: 20px;
}

button {
    height: 30px;
    width: 30px;
    border: none;
    border-radius: 2px;
    margin: 0 10px 5px 0;
}

button:hover {
    cursor: pointer;
}

.completed-icon {
    background-image: url('./icondone.png');
    background-size: cover;
}

.delete-icon {
    background-image: url('./icondel.png');
    background-size: cover;
}

.edit-icon {
    background-image: url('./iconedit.png');
    background-size: cover;
}

#root {
    background-color: #f7f7f7;
    width: 100vw;
    padding: 10px;
}

.container {
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
}

.task-form {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: space-between;
    font-family: Arial, Helvetica, sans-serif;
    margin: 40px 0 0 0;
}

.buttons {
    display: flex;
    flex-flow: row nowrap;
}

.task-row {
    display: flex;
    flex-flow: column wrap;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 20px 0 0 0;
    padding: 10px;
    background-color: rgba(200, 200, 200, 0.2);
    border-radius: 4px;
}

.task-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 0 20px;
    padding: 5px;
    height: 40px;
    width: 60px;
    background-color: green;
    color: #fff;
    border-radius: 4px;
    font-size: 2rem;
    font-weight: bold;
}

.task {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    text-align: left;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.5rem;
}

.completed {
    text-decoration: line-through;
    color: #666;
}

.banner1 {
    width: 90%;
    height: 90px;
    border: 1px solid #000;
    background-color: #fff;
    margin: 0 0 40px 0;
}

.banner2 {
    width: 90%;
    height: 90px;
    border: 1px solid #000;
    background-color: #fff;
    margin: 40px 0 50px 0;
}

.hint-block {
    margin: 300px 0 80px 0;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
}

.obs {
    margin: 50px 0 0 0;
    font-size: 1rem;
    color: #333;
    line-height: 1.2rem;
}


@media (min-width: 720px) {
    
    #root {
        width: 600px;
        overflow-x: hidden;
    }
   
}

@media (max-width: 719px) {
    
    #root {
        width: 90vw;
        margin: 5px;
        overflow-x: hidden;
    }
   
}

@media (max-width: 360px) {
    
    ::-webkit-scrollbar {
        width: 0px;
    }

    button {
        height: 50px;
        width: 50px;
        border: none;
        border-radius: 2px;
        margin: 0 20px 10px 0;
    }

    #root {
        width: 360px;
        padding: 10px;
    }

    .container {
        padding: 10px;
    }
   
    .task {
        display: flex;
        flex-flow: row wrap;
        align-items: center;
        text-align: left;
        font-family: Arial, Helvetica, sans-serif;
        font-size: 1.8rem;
    }

    .task-button {
        margin-right: 10px;
    }

}